<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">我的讨论</div>
    <el-tabs class="personnal-detail-wrap-tabs" v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="我的主题" name="first"></el-tab-pane>
      <el-tab-pane label="参与的主题" name="second"></el-tab-pane>
      <el-tab-pane label="我的评论" name="third"></el-tab-pane>
      <el-tab-pane label="我的分享" name="forth"></el-tab-pane>
    </el-tabs>
    <div class="personnal-detail-wrap-container">
      <common-card :option="cardOption" v-for="(c,i) in cards" :key="i" :datas="c"/>
    </div>
    <bottom-pager :total="total" @change="pageChange"/>
  </div>
</template>
<script>
import commonCard from "../../../components/personal/student/commonCard.vue";
import bottomPager from "../../../components/personal/bottomPager.vue";
export default {
  components: {
    commonCard,
    bottomPager
  },
  data() {
    return {
      total: 1,
      activeName: "first",
      cardOption: {
        icon: "icon-taolun2"
      },
      cards: [
        {
          title: "牛津英语词典解析",
          descs: [
            {
              text: "2019-04-10  15:32:46  发布"
            },
            {
              text: "共36人参与"
            },
            {
              text: "最后一条回复于 2019-04-10  19:55:27"
            }
          ]
        }
      ]
    };
  },
  methods: {
    tabClick() {},
    pageChange(currentPage) {}
  }
};
</script>
<style lang="less" scoped>
</style>



